.formContent{
	overflow-y: auto !important;
	height: 100vh;
	position: relative;
		.CompanyLogo {
		    text-align:center;
		    padding-top: 50px;
		    img{
		    	width: 145px;
    			height: 32px;
		    }
		}

		.none{
			display: none;
		}
		.ui-checkbox-checkmark{
			border: none !important;
		}
		.inputContent{
    		min-height: 560px;
			width: 500px;
			margin: 0 auto;
		    text-align: center;
		    padding: 0 50px;
		    p{
		    	font-family: $nsj;	
				font-size: 14px;
				color: #707070;
				padding: 0 10px;
			    font-weight: 100;
			    line-height: 1.8rem;
		    }
		    a.in_url{
		    	font-family: $nsj;	
				font-size: 14px;
				color:$blue;
			}
			a.in_url:hover{
				text-decoration: none;
			}
		    .trial{
		    	font-family: $nsj;	
			    background: $white;
			    -webkit-box-shadow: 0px 10px 20px 0px rgba(0,0,0,0.2);
			    -moz-box-shadow: 0px 10px 20px 0px rgba(0,0,0,0.2);
			    box-shadow: 0px 10px 20px 0px rgba(0,0,0,0.2);
			    width: 80%;
			    height: 50px;
			    border-radius: 40px;
			    color: $blue;
			    font-weight: bold;
			    border:none;
		    }

		    form{
			background: $white;
			padding: 40px;
			-webkit-box-shadow: 0px 10px 20px 0px rgba(0,0,0,0.2);
			-moz-box-shadow: 0px 10px 20px 0px rgba(0,0,0,0.2);
			box-shadow: 0px 10px 20px 0px rgba(0,0,0,0.2);
		    margin: 50px 0;
	        border-radius: 7px;

			h3{
				text-align: center;
				color: $darkerGray1;
				font-size: 20px;
				font-weight: 100;
		        margin-bottom: 30px;
    			margin-top: 0;	
    			font-family: $nsj;
    			line-height: 2.5rem;		
    		}
			.form-group {
			    margin-bottom: 1.5rem !important;
			    position: relative;
			}

			input{
				background: $lightGray5;
				border: none;
				border-radius: 7px;
				font-size: 14px;
			    width: 100%;
			    height: 50px;
			    padding: 0 10px;
			    padding-top: 20px;
			    font-family: $nsj;
			}

			input:focus {
			    color: #495057;
			    background-color: $lightGray5;
			    border-color: #3578E5;
			    outline: 0;
			    box-shadow: 0 0 0 1.3px #3578e596;
			}

			input:focus ~ .floating-label,
			input:not(:focus):valid ~ .floating-label{
			  top: 5px;
			  bottom: 10px;
			  left: 10px;
			  font-size: 10px;
			  opacity: 1;
			  font-family: $nsj;
			  color: #959FA8;
			}

			.inputText {
			  font-size: 12px;
			  width: 100%;
			  height: 50px;
			  font-family: $nsj;
			  color: #959FA8;
			}

			.floating-label {
			  position: absolute;
			  pointer-events: none;
			  left: 10px;
			  top: 18px;
			  transition: 0.2s ease all;
			  font-family: $nsj;
			  color: #959FA8;
			}

			.ui-checkbox-checkmark{
				background-color: $lightGray5 ;
				border-radius: 5px;
				height: 22px;
			  	width: 22px;
			  	cursor: pointer;
			}

			.ui-checkbox-checkmark:after{
				left: 8px!important;
    			bottom: 6px!important;
			}

			.ui-checkbox.checked .ui-checkbox-checkmark:before{
				background: #3577E5!important;
				border-radius: 5px;
			}

			.ui-checkbox-label-text {
			    margin-left: 8px;
			    font-size: 14px;
			    color: $darkGray;
			}

			button{
				font-family: $nsj;
				border-radius: 126px;
				color:$white;
				font-size: 15px;
				font-weight: 100;
				width: 100%;
			    height: 50px;
				border:none;
				cursor: pointer;
			    margin: 20px 0;
				-webkit-transition: all 200ms ease;
				transition: all 200ms ease;
				opacity: 90;
				box-shadow: 0 10px 30px 0 rgba(0, 68, 147, 0.3);
				background: #3577e5; 
				background: -moz-linear-gradient(left,  #3577e5 0%, #3577e5 32%, #004493 100%);
				background: -webkit-linear-gradient(left,  #3577e5 0%,#3577e5 32%,#004493 100%);
				background: linear-gradient(to right,  #3577e5 0%,#3577e5 32%,#004493 100%);
			}
			button.active{
			    text-decoration: none;
			    opacity: 1;
				background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.2)), to(rgba(0, 0, 0, 0.2))), -webkit-gradient(linear, left top, right top, from(#3577e5), to(#004493));
                background-image: linear-gradient(180deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), linear-gradient(90deg, #3577e5, #004493);
                box-shadow: 0 20px 30px 0 rgba(0, 68, 147, 0.3);
		        -webkit-transition: all 200ms ease;
				transition: all 200ms ease;
			}
		}
	}
}//endLoginID


// ====================================
//			MediaQueries
// ====================================

 @media (max-width:360px){
	.formContent {
		form {
		    padding: 25px;
		}
		.inputContent {
		  	width: 100%;
	  	    padding: 0 20px;
		}
	}
}//end

@media (min-width:360px) and (max-width:767px){
	.formContent {
		form {
		    padding: 25px;
		}
		.inputContent {
		  	width: 100%;
	  	    padding: 0 35px;
		}	
	}
}//end
